.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    padding: 15px;
}
.contentContainerOdd{
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px;
}
.contentContainerEven{
    display: flex;
    justify-content: space-between;
    flex-direction: row-reverse;
}
.content{
    text-align: center;
    padding: 50px 40px;
}
.illustration{
    max-width: 300px;
}
@media (max-width:700px){
    .illustration{
        max-width: 100px;
    }
}